import jquery = require("jquery");
import {AButton} from "./abstract/a-button";
import {Badge} from "./badge";
import {ELinkTarget} from "./enum/e-link-target";
import {EBasicColorStatus} from "../component/enum/e-basic-color-status";

/**
 * @classdesc Componente responsável por renderizar um button com um link
 *
 * <img src="../../doc_assets/linkbutton_example_1.png" />
 *  
 * @example
 * 
 * import {LinkButton,Badge,ELinkTarget} from "mvcomponents/button";
 * import {EBasicColorStatus} from "mvcomponents/component";
 * 
 * let linkButton:LinkButton = new LinkButton("Label Inicial")
 *     .setTarget(ELinkTarget.BLANK)
 *	   .setURL("http://www.mv.com.br"); 
 *	   .setIcon("mv-basico-dados_demograficos")
 *	   .setColor(EBasicColorStatus.PRIMARY)
 *	   .setLabel("Teste!")
 *	   .setTooltip("Tooltip do LinkButton");
 * 
 * let danger:Badge = new Badge("link teste!").setColor(EBasicColorStatus.DANGER);
 * linkButton.setBadge(danger);
 * 
 * @export
 * @extends {AButton}
 * @param {string} label Rótulo do LinkButton.
 * @memberof module:mvcomponents/button
 */
export class LinkButton extends AButton {
	constructor(label: string) {
		super("a", "<span class=\"linkbutton_label_\"></span>");
		jquery(this.element).attr("href","#").addClass("LinkButton ButtonComponent");
		this.setLabel(label);
	}
	/**
	 * Retorna o target do link.
	 * 
	 * ELinkTarget é um enum para os targets: BLANK,PARENT,SELF,TOP.
	 * Verificar o site para entender como irá abrir o link:
	 * {@link https://www.w3.org/TR/html4/types.html#type-frame-target}
	 * 
	 * @example
	 * 
	 * let linkButton1:LinkButton = new LinkButton("Label LinkButton")
	 *     .setTarget(ELinkTarget.BLANK);
	 * 
	 * let linkButton2:LinkButton = new LinkButton("Label LinkButton")
	 *     .setTarget(ELinkTarget.SELF);
	 *  
	 * let eLinkTarget:ELinkTarget = linkButton.getTarget();
	 * 
	 * @return {ELinkTarget}
	 */
	public getTarget():ELinkTarget{
		let target = jquery(this.element).attr("target");
		if(target){
			target = target.toUpperCase().replace("_","");
			return ELinkTarget[target];
		}
		return null;
	}
	/**
	 * 
	 * Altera o target do link.
	 * 
	 * ELinkTarget é um enum para os targets: BLANK,PARENT,SELF,TOP.
	 * Verificar o site para entender como irá abrir o link:
	 * {@link https://www.w3.org/TR/html4/types.html#type-frame-target}
	 * 
	 * @example
	 * 
	 * let linkButton:LinkButton = new LinkButton("Label LinkButton")
	 *  .setTarget(ELinkTarget.blank); 
	 * 
	 * @param {ELinkTarget} target Enum que indica como o link será aberto.
	 * @return {LinkButton}
	 */
	public setTarget(target:ELinkTarget):LinkButton{
		jquery(this.element)
			.attr("target",`_${ELinkTarget[target].toLowerCase()}`);
		return this;
	}
	/**
	 * 
	 * Altera a url do link.
	 *  
	 * @example
	 * 
	 * let linkButton:LinkButton = new LinkButton("Label LinkButton")
	 * .setURL("http://www.mv.com.br"); 
	 * let url:string = linkButton.getURL(); //Retorna "http://www.mv.com.br"
	 * 
	 * @return {string}
	 */
	public getURL():string{
		return jquery(this.element).attr("href");
	}
	/**
	 * 
	 * Altera a url do LinkButton.
	 *  
	 * @example
	 * 
	 * let linkButton:LinkButton = new LinkButton("Label LinkButton")
	 *  .setURL("http://www.mv.com.br"); 
	 * 
	 * @param {string} url Caminho que será aberto ao clicar no LinkButton.
	 * @return {LinkButton}
	 */
	public setURL(url:string):LinkButton{
		jquery(this.element).attr("href",url);
		return this;
	}
	/**
	 * Altera o ícone que será exibido pelo LinkButton.
	 * 
	 * @example
	 * 
	 * let linkButton:LinkButton = new LinkButton("Label LinkButton")
	 *     .setIcon("mv-basico-adicionar");
	 * 
	 * @param {string} psrc icone que será exibido
	 * @return {LinkButton}
	 */
	public setIcon(psrc: string): this {
		if (jquery(this.element).find("span.iconeinput:first").length === 0) {
			jquery(this.element)
				.append(`<span class="iconeinput ${psrc}"></span>`);
		} else {
			var classes = jquery(this.element)
					.find("span.iconeinput")
					.attr("class");
			jquery(this.element)
					.find("span.iconeinput")
					.removeClass()
					.addClass(psrc);
		}
		return this;
	}
	/**
	 * Altera o Badge do LinkButton.
	 * 
	 * @example
	 * 
	 * let badge:Badge = new Badge('label');
	 * 
	 * let linkButton:LinkButton = new LinkButton();
	 * linkButton.setBadge(badge);
	 * 
	 * @param {Badge} badge Badge do LinkButton
	 * @return {LinkButton}
	 */
	public setBadge(badge:Badge): this{
		if (jquery(this.element)
					.find("span.badge:first").length > 0
			){
			jquery(this.element)
				.find("span.badge")
				.remove();
		}
		jquery(this.element)
			.append(jquery(badge.element));
		return this;
	}
	/**
	 * Atribui ou substitui o rótulo do LinkButton
	 * 
	 * @example
	 * 
	 * let linkButton:LinkButton = new LinkButton("Label Original LinkButton");
	 * linkButton.setLabel("Label modificado");
	 * 
	 * @param {string} label Texto que deve substituir a label do botão.
	 * @return {LinkButton}
	 */
	public setLabel(label: string): this {
		jquery(this.element)
			.find("span.linkbutton_label_:first")
			.text(label);
		return this;
	}
	/**
	 * Informa a cor do botão.
	 * 
	 * Obs.: Utiliza o EBasicColorStatus do grupo 'mvcomponents/component' para definir a cor do LinkButton.
	 * 
	 * @example
	 * 
	 * let linkButton1:LinkButton = new LinkButton("DANGER").setColor(EBasicColorStatus.DANGER);
	 * let linkButton2:LinkButton = new LinkButton("SUCCESS").setColor(EBasicColorStatus.SUCCESS);
	 * let linkButton3:LinkButton = new LinkButton("INFO").setColor(EBasicColorStatus.INFO);
	 * let linkButton4:LinkButton = new LinkButton("PRIMARY").setColor(EBasicColorStatus.PRIMARY);
	 * let linkButton5:LinkButton = new LinkButton("WARNING").setColor(EBasicColorStatus.WARNING);
	 * 
	 * @param  {EBasicColorStatus} color EBasicColorStatus do grupo 'mvcomponents/component' para definir a cor do LinkButton.
	 * @return {LinkButton}
	 * 
	 */
	public setColor(color: EBasicColorStatus): this {
		jquery(this.element)
			.removeClass(`${this.getColors()} btn`)
			.addClass(`btn btn-${EBasicColorStatus[color].toLowerCase()}`);
		return this;
	}
	/**
	 * Atribui ou substitui o Tooltip do LinkButton
	 * 
	 * @example
	 * 
	 * let linkButton:LinkButton = new LinkButton("Label LinkButton");
	 * linkButton.setTooltip('Tooltip do LinkButton');
	 * 
	 * @param {string} tooltip Texto que deve ser exibido no Tooltip.
	 * @returns {LinkButton}
	 */
	public setTooltip(tooltip: string): this {
		if ( tooltip ) {
			jquery(this.element).attr("title", tooltip);
		} else {
			jquery(this.element).removeAttr("title");
		}
		return this;
	}
}
